@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&family=Dancing+Script:wght@700&display=swap);
* {
    box-sizing: border-box;
}
:root {
    --primary: #0d6efd;
    --secondary: #666666;
    --success: #198754;
    --danger: #dc3545;
    --warning: #ffc107;
    --info: #0dcaf0;
    --light: whitesmoke;
    --dark: #212529;
    --golden: #f0c040;
    --grey: #e1e1e1;
    --dark-teal: #13795b;
    --light-teal: #1aa179;
    --light-purple: #97ce;
    --dark-purple: #97ae;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
}
body {
    margin: 0;
    height: 100vh;
    font-size: 1.6rem;
    font-family: 'Ubuntu', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #e1e1e1;
    background-color: var(--grey);
}
#root {
  height: 100%;
}
.grid-container {
    display: grid;
    grid-template-areas: 
        'header'
        'main'
        'footer';
    grid-template-columns: 1fr;
    grid-template-rows: 5rem 1fr 5rem;
    height: 100%;
}
header {
    grid-area: header;
    background-color: #13795b;
    background-color: var(--dark-teal);
}
main {
    grid-area: main;
    padding: 1rem;
}
footer {
    grid-area: footer;
    background-color: #13795b;
    background-color: var(--dark-teal);
    color: #fff;
}

/* Common Classes */
h1 {
    font-size: 2.5rem;
    color: #212529;
    color: var(--dark);
}
h2 {
    font-size: 1.7rem;
    padding: 1rem 0;
}
a {
    text-decoration: none;
}
a:hover {
    color: #f0c040;
    color: var(--golden);
}
input,
select,
textarea,
button {
    padding: 1rem;
    outline: none;
    border-radius: 0.6rem;
    border: 0.1rem solid #a4a4a4;
    font-size: 1.6rem;
    font-family: 'Ubuntu', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: all 0.3s ease;
}
button {
    background-color: #f8f8f8;
    cursor: pointer;
}
input:hover,
select:hover,
textarea:hover,
input:focus,
select:focus,
textarea:focus {
    border: 0.1rem solid #666666;
    border: 0.1rem solid var(--secondary);
}

button.primary {
    background-color: #97ce;
    background-color: var(--light-purple);
    font-weight: 500;
    text-transform: uppercase;
    color: white;
}
button.block {
    width: 100%;
}
button.small {
    font-size: 1.2rem;
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.row.center {
  -webkit-justify-content: center;
          justify-content: center;
}

.row.top {
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.col-1 {
  -webkit-flex: 1 1 25rem;
          flex: 1 1 25rem;
}

.col-2 {
  -webkit-flex: 2 1 50rem;
          flex: 2 1 50rem;
}

.h-100 {
  height: 100%;
}

.min-30 {
  min-width: 30rem;
}

.gap-2 {
  grid-gap: 2rem;
  gap: 2rem;
}

.grid-center {
  display: grid;
  place-items: center;
}

.subtitle {
  color: var(--dark);
  text-transform: uppercase;
  border-bottom: 1px solid #d4d4d4;
}

.max-width {
  max-width: 1200px;
  margin: auto;
  padding-inline: 1rem;
  height: 100%;
}

.bg-white {
  background-color: white;
}

.line {
  height: 1px;
  width: 100%;
  background-color: var(--secondary);
  opacity: 50%;
  margin-block: 1.5rem;
}

/* margin */
.mb-0 {
  margin-bottom: 0;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 2rem;
}
.card {
  background-color: var(--light);
  padding: 0.5rem;
  transition: all 0.3s ease;
}

.card.product {
  width: 270px;
  margin: 1rem;
}

.card.product:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 13px -5px, rgba(0, 0, 0, 0.3) 0px 8px 8px -8px;
}

.card-body {
  padding: 1rem;
}
img.small {
  max-width: 15rem;
  width: 100%;
}

img.medium {
  max-width: 29rem;
  width: 100%;
}

img.large {
  height: 60rem;
  display: block;
  margin: auto;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

li {
  margin-bottom: 2rem;
}

.cart-price li {
  margin-top: 2rem;
}
.form {
  max-width: 60rem;
  margin: 0 auto;
}

.form>div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 1rem;
}

.form label {
  margin: 1rem 0;
}
header a {
  color: var(--light);
  padding: 1rem;
}

header .logo h1 {
  font-family: 'Dancing Script', cursive;
  font-size: 2.5rem;
  line-height: 1rem;
  font-weight: 700;
  color: var(--light);
  margin: 0;
  padding: 0;
}

.badge {
  background-color: var(--danger);
  color: var(--light);
  border-radius: 50%;
  padding: 0.2rem 0.5rem;
  font-size: 1.4rem;
  position: relative;
  top: -1rem;
}
.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  position: absolute;
  top: 4rem;
  right: 0;
  min-width: 12rem;
  padding: 1rem;
  z-index: 1;
  background-color: var(--dark-teal);
  border-radius: 0.6rem;
  -webkit-animation: appear 0.3s;
          animation: appear 0.3s;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, 
  rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

@-webkit-keyframes appear {
  from { opacity: 0.2; }
  to { opacity: 1}
}

@keyframes appear {
  from { opacity: 0.2; }
  to { opacity: 1}
}
.price {
  font-size: 2.5rem;
  color: var(--dark);
  margin-bottom: 0;
}

.mrp {
  text-decoration: line-through;
  padding-inline: 1rem;
  color: var(--secondary);
}

.discount {
  font-weight: 500;
  color: #ff905a;
}

.price-info {
  margin-top: 0.5rem;
  font-size: 1.5rem;
  font-weight: 500;
  color: #03a685;
}
.checkout-steps>div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 1rem;
  margin: 0.1rem;
  font-weight: bold;
}

.checkout-steps>div.active {
  border-top-color: #f08000;
  color: #f08000;
}
.loading {
  display: block !important;
}
.success {
  color: var(--success);
}

.danger {
  color: var(--danger);
}

.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.6rem;
}

.alert-info {
  color: var(--info);
  background-color: #e0e0ff;
}

.alert-danger {
  color: var(--danger);
  background-color: #ffe0e0;
}

.alert-success {
  color: var(--success);
  background-color: #e0ffe0;
}
.product-info {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.product-desc>strong {
  text-transform: uppercase;
  color: var(--dark);
}

.product-desc>p {
  margin-top: 0.7rem;
  color: var(--secondary);
}

.cart-item {
  position: relative;
}

.delete-btn {
  border: none;
  background: none;
  position: absolute;
  top: 1rem;
  right: 2rem;
}

.rating {
  background-color: white;
  padding: 0.5rem 1rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.rating span {
  color: var(--dark);
  font-weight: 500;
}

.rating span:first-child {
  border-right: 1px solid var(--dark);
  padding-right: 0.7rem;
}

.rating span:last-child {
  font-weight: 400;
  padding-left: 0.7rem;
}
.table {
  width: 100%;
  border-collapse: collapse;
}

.table thead {
  background-color: var(--light-purple);
  color: var(--light);
}

.table tbody tr {
  background-color: var(--light);
}

.table tbody tr:nth-of-type(odd){
  background-color: #97c1;
}

.table td,
.table th {
  text-align: center;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}

.table th {
  padding: 1rem;
}

.table button {
  margin: 0 0.2rem;
}
@media screen and (min-width: 768px) {
  .signin{
    min-width: 50rem;
  }
}
@media screen and (min-width: 768px) {
  .signup {
    min-width: 50rem;
  }
}
.shipping {
  width: 100%;
  margin: auto;
}

@media screen and (min-width: 768px) {
  .shipping {
    width: -webkit-max-content;
    width: max-content;
    min-width: 50rem;
  }
}
.payment-method {
  width: 100%;
  margin: auto;
}

@media screen and (min-width: 768px) {
  .payment-method {
    width: -webkit-max-content;
    width: max-content;
    min-width: 50rem;
  }
}
@media screen and (min-width: 768px) {
  .profile {
    min-width: 50rem;
    min-height: 53rem;
  }
}
@media screen and (min-width: 768px) {
  .product-edit {
    min-width: 50rem;
    min-height: 50rem;
  }
}
