.card {
  background-color: var(--light);
  padding: 0.5rem;
  transition: all 0.3s ease;
}

.card.product {
  width: 270px;
  margin: 1rem;
}

.card.product:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 13px -5px, rgba(0, 0, 0, 0.3) 0px 8px 8px -8px;
}

.card-body {
  padding: 1rem;
}